.programs {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 0 2rem;

  .programsHeader {
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
  }

  .programsCategories {
    display: flex;
    gap: 1rem;

    .category {
      display: flex;
      flex-direction: column;
      background-color: gray;
      padding: 2rem;
      gap: 1rem;
      color: white;
      justify-content: space-between;

      &:hover {
        background: var(--planCard);
        cursor: pointer;
      }

      :nth-child(1) {
        width: 2rem;
        height: 2rem;
        fill: white;
      }

      :nth-child(2) {
        font-size: 1rem;
        font-weight: bold;
      }

      :nth-child(3) {
        font-size: 0.9rem;
        line-height: 25px;
      }

      .joinNow {
        display: flex;
        align-items: center;
        gap: 2rem;

        img {
          width: 1rem;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .programs {
    .programsHeader {
      flex-direction: column;
      gap: 1rem;
      font-size: x-large;
      align-items: center;
      justify-content: center;
      margin-top: 2rem;
    }

    .programsCategories {
      flex-direction: column;
    }
  }
}
